/* global window navigator document */
import BookmarkIcon from '@buzzguru/ui/assets/icons/bookmark';
import BookmarkEmptyIcon from '@buzzguru/ui/assets/icons/bookmark-empty';
import ExtBattaryIcon from '@buzzguru/ui/assets/icons/ext-battary';
import ExtBrightnessIcon from '@buzzguru/ui/assets/icons/ext-brightness';
import Copy from '@buzzguru/ui/assets/icons/ext-copy';
import ExtLaunchIcon from '@buzzguru/ui/assets/icons/ext-launch-outlined';
import ExtLightIcon from '@buzzguru/ui/assets/icons/ext-light';
import ExtLogoutIcon from '@buzzguru/ui/assets/icons/ext-logout';
import ExtMoonIcon from '@buzzguru/ui/assets/icons/ext-moon';
import Close from '@buzzguru/ui/assets/icons/ext-shrink';
import Tooltip from '@buzzguru/ui/Tooltip';
import Button from '@lskjs/button';
import ButtonGroup from '@lskjs/button/ButtonGroup';
import Link from '@lskjs/link';
import Modal from '@lskjs/modal';
import T from '@lskjs/t';
import get from 'lodash/get';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useContext, useEffect, useState } from 'react';

import { ToolbarContext } from '../../../Toolbar';
import generateClipboardData from '../../../utils/generateClipboardData';
import getRequestParams from '../../../utils/getRequestParams';
import Avatar from '../../Avatar';
import Login from './Login';
import {
  ButtonsThemeWrapper,
  ButtonsWrapper,
  CloseButton,
  HoverButton,
  IconWrapper,
  ListButton,
  MainButton,
  ModalContent,
  ModalItem,
  Settings,
  StyledButton,
  TitleWrapper,
  Typography,
  UserWrapper,
  Wrapper,
} from './RightContent.styles';

const RightContent = ({ app, entity, login, extensionFavorite, i18, ...props }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const session = window?.__lskjs?.GM_getValue('session') || null;
  const isAuth = session;
  // console.log({ isAuth });
  const ctx = useContext(ToolbarContext);

  const getPlanKey = (plan) => {
    switch (plan) {
      case 'free':
        return 'base_month';
      case 'base':
        return 'pro_month';
      case 'pro':
        return 'aio_month';
      default:
        return 'aio_month';
    }
  };

  let listType = 'channels';
  if (type === 'youtubeVideo') {
    listType = 'youtubeVideos';
  }
  if (type === 'twitch') {
    listType = 'twitchChannels';
  }
  if (type === 'twitchStream') {
    listType = 'twitchStreams';
  }
  if (type === 'instagram') {
    listType = 'instagramChannels';
  }
  if (type === 'tiktok') {
    listType = 'tiktokChannels';
  }

  const onClose = () => {
    ctx.triggerOpen();
  };
  const logout = () => {
    window?.__lskjs?.GM_setValue('session', null);
    window?.__lskjs?.GM_setValue('token', null);
    console.log('authToken', window?.__lskjs?.GM_getValue('session'));
    app.redirect('');
  };
  const handleCopy = () => {
    let channel = item;
    if (type === 'youtubeVideo' || type === 'twitchStream') {
      channel = item.channel;
    }
    const stats = generateClipboardData(channel, i18, type);
    const keys = stats.flatMap((stat) => Object.keys(stat)).join('\t');
    const values = stats.flatMap((stat) => Object.values(stat)).join('\t');
    const table = [keys, values].join('\n');
    navigator.clipboard.writeText(table);
    return window?.siiimpleToast?.message ? window?.siiimpleToast?.message('Copied!') : console.log('Copied!');
  };
  const handleListAdd = async () => {
    try {
      await extensionFavorite({ _id: item?._id, type: listType });
      window?.siiimpleToast?.message ? window?.siiimpleToast?.message('Added to list!') : console.log('Added to list!');
    } catch (e) {
      console.log({ error: e });
    }
    return app.redirect('');
  };
  const handleRemoveListItem = () =>
    window?.siiimpleToast?.message
      ? window?.siiimpleToast?.message('Already in list!')
      : console.log('Already in list!');
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, [document?.location?.href]);
  let href = `https://buzzguru.com/${type}/channels/${item?._id || ''}`;
  if (type === 'youtubeVideo') href = `https://buzzguru.com/videos/${item?._id || ''}`;
  if (type === 'twitchStream') href = `https://buzzguru.com/streams/${item?._id || ''}`;

  const displayOptions = [
    {
      value: 'long',
      title: <T name="extention.settings.display.long" />,
    },
    {
      value: 'short',
      title: <T name="extention.settings.display.short" />,
    },
  ];
  const themeOptions = [
    {
      value: 'auto',
      title: <T name="extention.settings.theme.auto" />,
      icon: <ExtBrightnessIcon size={16} />,
    },
    {
      value: 'dark',
      title: <T name="extention.settings.theme.dark" />,
      icon: <ExtMoonIcon size={16} />,
    },
    {
      value: 'light',
      title: <T name="extention.settings.theme.light" />,
      icon: <ExtLightIcon size={16} />,
    },
  ];
  return (
    <Wrapper {...props}>
      {isAuth ? (
        <>
          {item?._id ? (
            <>
              <Tooltip id={`ex-copy`} place="top" overlay={i18.t('extension.button.copy')}>
                <HoverButton
                  extView={ctx?.view}
                  view="empty"
                  icon={<Copy size={24} />}
                  isRipple={false}
                  onClick={handleCopy}
                  style={{ marginRight: 8 }}
                />
              </Tooltip>
              {item?.inFavoriteList ? (
                <>
                  {ctx?.view === 'short' ? (
                    <MainButton
                      // onClick={handleRemoveListItem}
                      componentClass={Link}
                      target="_blank"
                      href="https://buzzguru.com/lists"
                      iconLeft={<BookmarkIcon />}
                      paint="primary"
                      style={{ marginRight: 8, padding: '9px 12px' }}
                    >
                      <T name="channel.headerInfo.inList" />
                    </MainButton>
                  ) : (
                    <Tooltip id={`ex-copy`} place="top" overlay={i18.t('channel.headerInfo.inList')}>
                      <HoverButton
                        extView={ctx?.view}
                        view="empty"
                        icon={<BookmarkIcon size={24} />}
                        isRipple={false}
                        componentClass={Link}
                        target="_blank"
                        href="https://buzzguru.com/lists"
                        style={{ marginRight: 8, color: '#fff' }}
                      />
                    </Tooltip>
                  )}
                </>
              ) : (
                <>
                  {ctx?.view === 'short' ? (
                    <ListButton
                      onClick={handleListAdd}
                      iconLeft={<BookmarkEmptyIcon />}
                      paint="primary"
                      view="text"
                      bordered
                      style={{ marginRight: 8, padding: '7px 12px' }}
                    >
                      <T name="button.addToList" />
                    </ListButton>
                  ) : (
                    <Tooltip id={`ex-copy`} place="top" overlay={i18.t('button.addToList')}>
                      <HoverButton
                        extView={ctx?.view}
                        view="empty"
                        icon={<BookmarkEmptyIcon size={24} />}
                        isRipple={false}
                        onClick={handleListAdd}
                        style={{ marginRight: 8, color: '#fff' }}
                      />
                    </Tooltip>
                  )}
                </>
              )}
              <MainButton
                as={Link}
                paint="primary"
                href={href}
                target="_blank"
                style={{ marginRight: 8, padding: '11px 24px', color: '#fff' }}
              >
                <T name="extention.button.moreInsights" />
              </MainButton>
            </>
          ) : (
            ''
          )}
          <Modal size={364} trigger={<HoverButton view="empty" icon={<Settings size={18} />} isRipple={false} />}>
            <ModalContent style={{ borderRadius: 8, display: 'block' }}>
              <TitleWrapper>
                <Typography
                  view="extra-1"
                  lineHeight="24px"
                  letterSpacing="-0.01em"
                  style={{ marginLeft: 16, marginBottom: 16 }}
                >
                  <T name="extention.settings.title" />
                </Typography>
              </TitleWrapper>
              <UserWrapper>
                <Avatar
                  id="user-profile-avatar"
                  size={80}
                  src={session?.company?.about?.image}
                  cspStyle={{ borderRadius: '50px' }}
                  defaultAvatar="https://buzzguru.com/assets/images/blogger-big.png"
                  style={{ marginBottom: 10 }}
                />
                <Typography view="extra-1" color="#fff" style={{ marginBottom: 1 }}>
                  {`${session?.user?.firstName || ''} ${session?.user?.lastName || ''}`}
                </Typography>
                <Typography
                  as={Link}
                  href="https://buzzguru.com/settings/account"
                  view="title-2"
                  paragraph={16}
                  target="_blank"
                  style={{ color: '#7070ff', textDecoration: 'none' }}
                >
                  <T name="extention.settings.manage" />
                </Typography>
                {['free', 'base', 'pro'].includes(session?.company?.tariff || '') ? (
                  <Button
                    as={Link}
                    href={`https://buzzguru.com/settings/subscriptions/tariff/checkout?key=${getPlanKey(
                      session?.company?.tariff,
                    )}`}
                    paint="primary"
                    target="_blank"
                    style={{ padding: '10px 16px', color: '#fff' }}
                  >
                    <T name="extention.settings.upgrade" />
                  </Button>
                ) : (
                  ''
                )}
              </UserWrapper>

              <ButtonGroup panel>
                <ButtonsThemeWrapper>
                  {themeOptions.map((el, index) => (
                    <StyledButton
                      key={index} // eslint-disable-line react/no-array-index-key
                      // size="small"
                      type="button"
                      iconLeft={el.icon}
                      active={ctx.theme === el.value}
                      onClick={() => {
                        ctx.setTheme(el.value);
                      }}
                    >
                      {el.title}
                    </StyledButton>
                  ))}
                </ButtonsThemeWrapper>
              </ButtonGroup>
              <ModalItem>
                <div style={{ display: 'flex', alignItems: 'center', whiteSpace: 'nowrap', flexDirection: 'row' }}>
                  <IconWrapper>
                    <ExtBattaryIcon color="#7070ff" size={16} />
                  </IconWrapper>
                  <Typography view="title-2" color="#fff">
                    <T name="extention.settings.displayMode" />
                  </Typography>
                </div>
                <ButtonGroup panel>
                  <ButtonsWrapper>
                    {displayOptions.map((el, index) => (
                      <StyledButton
                        key={index} // eslint-disable-line react/no-array-index-key
                        size="small"
                        type="button"
                        active={ctx.view === el.value}
                        onClick={() => {
                          ctx.setView(el.value);
                        }}
                      >
                        {el.title}
                      </StyledButton>
                    ))}
                  </ButtonsWrapper>
                </ButtonGroup>
              </ModalItem>
              <ModalItem>
                <div style={{ display: 'flex', alignItems: 'center', whiteSpace: 'nowrap', flexDirection: 'row' }}>
                  <IconWrapper>
                    <Copy color="#7070ff" size={16} />
                  </IconWrapper>
                  <Typography view="title-2" color="#fff">
                    <T name="extention.settings.сopyFormat" />
                  </Typography>
                </div>
                <Link
                  href="https://help.buzzguru.com/how-to-use-buzzguru-chrome-extension"
                  target="_blank"
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    whiteSpace: 'nowrap',
                    flexDirection: 'row',
                    textDecoration: 'none',
                  }}
                >
                  <Typography view="title-2" color="#98989B" style={{ marginRight: 8 }}>
                    <T name="extention.settings.tutorial" />
                  </Typography>
                  <ExtLaunchIcon color="#DADADA" />
                </Link>
              </ModalItem>
              <ModalItem as={Button} view="empty" style={{ marginBottom: 10, padding: 0 }} onClick={logout}>
                <div style={{ display: 'flex', alignItems: 'center', whiteSpace: 'nowrap', flexDirection: 'row' }}>
                  <IconWrapper>
                    <ExtLogoutIcon color="#7070ff" size={16} />
                  </IconWrapper>
                  <Typography view="title-2">
                    <T name="extention.settings.logout" />
                  </Typography>
                </div>
              </ModalItem>
            </ModalContent>
          </Modal>
        </>
      ) : (
        <Login login={login} type={type} />
      )}
      <CloseButton view="empty" icon={<Close size={24} />} onClick={onClose} isRipple={false} />
    </Wrapper>
  );
};

RightContent.propTypes = {
  app: PropTypes.objectOf(Object).isRequired,
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
  login: PropTypes.func.isRequired,
  extensionFavorite: PropTypes.func.isRequired,
};

export default inject('i18')(RightContent);
