/* eslint-disable no-undef */
export * from '@lskjs/env';
export const stage = window?.__lsk?.stage || window?.STAGE || 'prod';
export const isProd = stage === 'prod' || stage === 'production';
export const isDebug = !isProd;
export const version = window?.__lsk?.version || window?.VERSION || '???';
export const plugins = window?.__lsk?.plugins || window?.PLUGINS || ['*'];
export const { isExtension } = window;
export const isUserscript = !window?.isExtension; // TODO: вообще не корректно, но надо подумать как грамотнее это делать

export const build = isUserscript ? `US-${version}-${stage}` : `EXT-${version}-${stage}`;
